<template>
    <div>
        <img class="card-img-top" src="../assets/free_pen.jpg" alt="Card image cap">
        <div class="card-body">
            <h5 class="card-title">{{book['title']}}</h5>
            <p class="card-text">{{book['body']}}</p>
            <a @click="deleteBook" class="btn btn-danger btn-custom">Delete</a>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    props : ["book"],
    methods : {
        async deleteBook(){
            const payload = {
                "id" : this.book['_id']
            }
            //Delete Book From Server
            await axios.post(`${this.$hostname}/deleteBook`,payload);

            this.$emit('refresh');
        }
    }
}
</script>

